'use client';

import React, { useRef, useState } from 'react';
import Link from 'next/link';
import { motion, useInView, AnimatePresence } from 'framer-motion';
import { Section, Card } from '../ui';
import { writing } from '../../config/content';

const Writing = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: '-10% 0px' });
  const [isExpanded, setIsExpanded] = useState(false);

  const pinnedArticle = writing[0];
  const otherArticles = writing.slice(1);

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.5, ease: [0.25, 0.4, 0.25, 1] },
    },
  };

  return (
    <Section id="writing">
      <motion.div 
        className="mb-14"
        initial={{ opacity: 0, y: 20 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true }}
        transition={{ duration: 0.5 }}
      >
        <h2 className="text-h1 font-bold text-text-primary">Writing</h2>
      </motion.div>

      {/* Pinned Article */}
      <motion.div
        ref={ref}
        initial="hidden"
        animate={isInView ? "visible" : "hidden"}
        variants={itemVariants}
        className="mb-8"
      >
        <Link href={pinnedArticle.url} className="block group">
          <Card hover className="cursor-pointer relative overflow-visible">
            {/* 3D Pin - angled */}
            <div 
              className="absolute -top-2 left-6 z-10"
              style={{ transform: 'rotate(-30deg)' }}
            >
              <div className="relative">
                {/* Pin shadow - offset for angle */}
                <div 
                  className="absolute w-6 h-6 bg-black/40 rounded-full blur-md"
                  style={{ top: '8px', left: '6px' }}
                />
                {/* Pin head */}
                <div 
                  className="relative w-7 h-7 rounded-full"
                  style={{
                    background: 'linear-gradient(135deg, #f87171 0%, #ef4444 40%, #dc2626 100%)',
                    boxShadow: 'inset -2px -2px 4px rgba(0,0,0,0.3), inset 2px 2px 4px rgba(255,255,255,0.3), 0 4px 12px rgba(0,0,0,0.5)',
                  }}
                >
                  {/* Shine highlight */}
                  <div 
                    className="absolute top-1.5 left-1.5 w-2.5 h-2.5 rounded-full bg-white/50"
                  />
                </div>
                {/* Pin needle - angled into the card */}
                <div 
                  className="absolute w-1.5 h-4 rounded-b-full"
                  style={{
                    top: '24px',
                    left: '50%',
                    transform: 'translateX(-50%)',
                    background: 'linear-gradient(to bottom, #d1d5db, #9ca3af, #6b7280)',
                    boxShadow: '2px 2px 4px rgba(0,0,0,0.4)',
                  }}
                />
              </div>
            </div>
            <h3 className="text-xl font-semibold text-text-primary mb-3 group-hover:text-accent transition-colors duration-200">
              {pinnedArticle.title}
            </h3>
            <p className="text-text-secondary text-sm leading-relaxed mb-5">
              {pinnedArticle.description}
            </p>
            <span className="text-accent text-sm font-medium inline-flex items-center gap-1 group-hover:gap-2 transition-all duration-200">
              Read more
              <span>→</span>
            </span>
          </Card>
        </Link>
      </motion.div>

      {/* Expandable Section */}
      {otherArticles.length > 0 && (
        <div>
          <motion.button
            onClick={() => setIsExpanded(!isExpanded)}
            className="flex items-center gap-2 text-text-secondary hover:text-text-primary transition-colors duration-200 mb-6"
            initial={{ opacity: 0 }}
            animate={isInView ? { opacity: 1 } : { opacity: 0 }}
            transition={{ delay: 0.3 }}
          >
            <motion.span
              animate={{ rotate: isExpanded ? 90 : 0 }}
              transition={{ duration: 0.2 }}
              className="text-lg"
            >
              →
            </motion.span>
            <span className="text-sm font-medium">
              {isExpanded ? 'Hide' : 'More'} articles ({otherArticles.length})
            </span>
          </motion.button>

          <AnimatePresence>
            {isExpanded && (
              <motion.div
                initial={{ height: 0, opacity: 0 }}
                animate={{ height: 'auto', opacity: 1 }}
                exit={{ height: 0, opacity: 0 }}
                transition={{ duration: 0.3, ease: [0.25, 0.4, 0.25, 1] }}
                className="overflow-hidden"
              >
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {otherArticles.map((post, index) => (
                    <motion.div
                      key={index}
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: index * 0.1 }}
                    >
                      <Link href={post.url} className="block group h-full">
                        <Card hover className="cursor-pointer h-full flex flex-col">
                          <h3 className="text-lg font-semibold text-text-primary mb-3 group-hover:text-accent transition-colors duration-200">
                            {post.title}
                          </h3>
                          <p className="text-text-secondary text-sm leading-relaxed mb-5 line-clamp-2 min-h-[2.5rem]">
                            {post.description}
                          </p>
                          <span className="text-accent text-sm font-medium inline-flex items-center gap-1 group-hover:gap-2 transition-all duration-200 mt-auto">
                            Read more
                            <span>→</span>
                          </span>
                        </Card>
                      </Link>
                    </motion.div>
                  ))}
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      )}
    </Section>
  );
};

export default Writing;
